<div class="home"><el-row :gutter="20"><el-col :span="6"><div class="navigation-container"><h2>Region</h2><el-button type="primary" round @click="changeRegion('TWN')">Taiwan</el-button><el-button type="primary" round @click="changeRegion('US')">USA</el-button><div>----------</div><el-button type="primary" round @click="changeRegion('IDN')">Indonesia</el-button><el-button type="primary" round @click="changeRegion('THA')">Thailand</el-button><h2>Select date</h2><el-date-picker class="nav-select" v-model="fireDate" type="date" :clearable="false"></el-date-picker><h2>Player</h2><el-button type="primary" round @click="jumpToToday()">Today</el-button><el-button type="primary" round @click="playAni(2)">2 years</el-button><div class="date-text">{{currentDate}}</div></div></el-col><el-col :span="18"><FireMap :fireplaces="fireplaces" :region="region"></FireMap></el-col></el-row></div>